// COMPONENT - PROFILE PAGE
const ProfilePage = ({setPage}) => {
	
	const { sso }  = useSelector(state => state);
	const dispatch = useDispatch()
    const { user,evsdata } = sso;
	const { setting } = useSelector( state => state.theme )
	const { REACT_APP_THEME_TAG : themetag } = process.env; 
	const ui = setting.clients.find( r => r.tag === themetag )
    const [ Logo,setLogo ] = useState(null)
	const [ voter,setVoter ] = useState({})
	const { REACT_APP_API_URL : API_URL } = process.env; 
	
	const getPortfolio = (name) => {
		return evsdata && evsdata.candidates.filter( r => r.portfolio == name)
	}

	const choose = (e,pid,cid) => {
		e.preventDefault();
        setVoter({ ...voter,[pid]:cid })
	}

	const chosenIcon = (pid,cid) => {
        return voter[pid] == cid
	}
	
	const submitVote = async (e,id) => {
		e.preventDefault();
		const data = { id, tag:user.user.staff_no, name:user.user.name, votes:voter }
	    const resp = await postEvsData(data)
	    console.log(resp);
		if(resp.success){
			//dispatch(setEvsdata({ allowAccess:false }));
			dispatch(updateAlert({ show:true, message:`VOTE HAS SUBMITTED SUCCESSFULLY!`, type:'success' }))
			setTimeout(() => setPage(null),3000)
		}else{
			dispatch(updateAlert({ show:true, message:resp.msg.toUpperCase(), type:'error' }))
			setTimeout(() => setPage(null),3000)
		}
	}

	useEffect(() => {
		import(`../../../assets/img/logo/${themetag}/logo.png`).then(src => setLogo(src.default))
	},[])

	return (
	   <div className="content-area card">
		<div className="card-innr">
				<div className="token-statistics card card-token height-auto">
					<div className="card-innr">
						<div className="token-balance">
							<div className="token-balance-text">
								<span className="lead">VOTING SIMULATION</span>
								<Link onClick={(e) => submitVote(e,evsdata && evsdata.id)} class="btn btn-sm btn-outline btn-light print-btn vote-btn"><i class="fa fa-3x fa-send"></i> <b >SUBMIT FINAL VOTE</b></Link>
							</div>
						</div>
					</div>
				</div>
			{/* CONTENT HERE */}
			<div className="content-area card px-2 bg-white">
				
				<div style={{ display:'none'}}>
					<div className="card-head">
					   <h3 className="card-title text-center text-dark">{ui.tag.toUpperCase()} </h3>
					</div>
					<h3 className="card-sub-title text-center">{ui.name.toUpperCase()}</h3>
					<center><img src={`${API_URL}/photos/evs/?tag=logo&eid=${evsdata && evsdata.id}`} className="app-logo" style={{width:'80px'}} /></center>
					<div className="gaps-1-5x" />
				</div>

               { evsdata && evsdata.portfolios.map((row,i) => 
				<span className="portfolio" key={i}>
					<div className="data-details d-md-flex mb-2 mt-2">
						<h5 className="card-sub-title mb-2 portfolio_title">{row.name}</h5>
						{/*<div className="fake-class"><span className="data-details-title">SHOULD UTAG-UCC ACCEPT NEC'S DECISION TO SUSPEND THE STRIKE? </span></div>*/}
					</div>
					
					{ getPortfolio(row.name) && getPortfolio(row.name).map((r,j) =>
					<div className="candidates" key={r.id}>
						<div className="candbio">
						    <img src={`${API_URL}/photos/evs/?tag=${r.id}&eid=${r.id}`} alt="Candidate"/>
							<span>{r.name}</span>
						</div>
						<div className="votecover" onClick={(e)=> choose(e,row.id,r.id)}>
					   	    { chosenIcon(row.id,r.id) && <img src={Thumb} alt="Candidate"/> }
							{ !chosenIcon(row.id,r.id) && <button>CHOOSE</button> }
						</div>
					</div>
					)}
				</span>
			   )}
				

			</div>
			</div>
	</div>
	)
}


export default EVSVote;
